ont-style: normal;\n    font-weight: 400;\n    font-size: 24.7422px;\n    line-height: 37px;\n    text-align: center;\n    text-transform: capitalize;\n    color: #ffffff;\n}\n.guest-banner-slogan > div:nth-child(2) {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 14.8453px;\n    line-height: 22px;\n    text-align: center;\n    text-transform: lowercase;\n\n    color: #dfdfdf;\n}\n@media screen and (max-width: 1600px) {\n    .guest-banner-cont {\n        padding-top: 50%;\n    }\n}\n@media screen and (max-width: 1300px) {\n    .guest-banner-cont {\n        padding-top: 60%;\n    }\n}\n@media screen and (max-width: 1200px) {\n    .guest-banner-cont {\n        padding-top: 70%;\n    }\n}\n@media screen and (max-width: 991px) {\n    .guest-banner-cont {\n        padding-top: 80%;\n    }\n    .guest-banner-timer {\n        margin: 22px auto;\n    }\n    .guest-banner-timer-single {\n        width: 76px;\n        height: 76px;\n    }\n    /* .guest-banner-timer-single > span:nth-child(1) {\n        font-size: 60px;\n        line-height: 60px;\n    } */\n    .guest-banner-content {\n        top: 3%;\n    }\n    .wed-host-section-container {\n        padding: 4em 0 !important;\n        padding-bottom: 6em !important;\n    }\n}\n@media screen and (max-width: 768px) {\n    .guest-banner-cont {\n        padding-top: 100%;\n    }\n    .guest-banner-couple-name {\n        margin-top: 1px;\n    }\n    .guest-banner-cont {\n        padding-top: 83%;\n    }\n}\n@media screen and (max-width: 576px) {\n    .guest-banner-couple-name > span {\n        font-weight: 400;\n        font-size: 18.2232px;\n        line-height: 24px;\n    }\n    .guest-banner-couple-name > span:nth-child(2) {\n        font-weight: 400;\n        font-size: 33.1331px;\n        line-height: 49px;\n    }\n    .guest-banner-venue-date {\n        font-weight: 500;\n        font-size: 10px;\n        line-height: 15px;\n    }\n    .guest-banner-timer-single > span:nth-